html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%; }

body {
  font-family: 'Montserrat', sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.main {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1100px;
  width: 100%;
  padding: 0 16px 0;
  margin: 0 auto; }

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  border: 1px solid black;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

.section__left {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #1d1d32;
  overflow: hidden; }

section .air {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: url(https://1.bp.blogspot.com/-xQUc-TovqDk/XdxogmMqIRI/AAAAAAAACvI/AizpnE509UMGBcTiLJ58BC6iViPYGYQfQCLcBGAsYHQ/s1600/wave.png);
  background-size: 1000px 100px; }

section .air.air1 {
  animation: wave 30s linear infinite;
  z-index: 1000;
  opacity: 1;
  animation-delay: 0s;
  bottom: 0; }

section .air.air2 {
  animation: wave2 15s linear infinite;
  z-index: 999;
  opacity: 0.5;
  animation-delay: -5s;
  bottom: 10px; }

section .air.air3 {
  animation: wave 30s linear infinite;
  z-index: 998;
  opacity: 0.2;
  animation-delay: -2s;
  bottom: 15px; }

section .air.air4 {
  animation: wave2 5s linear infinite;
  z-index: 997;
  opacity: 0.7;
  animation-delay: -5s;
  bottom: 20px; }

@keyframes wave {
  0% {
    background-position-x: 0px; }
  100% {
    background-position-x: 1000px; } }

@keyframes wave2 {
  0% {
    background-position-x: 0px; }
  100% {
    background-position-x: -1000px; } }

.section {
  display: flex;
  justify-content: space-between; }
  .section__left {
    flex: 1 1 50%; }
  .section__right {
    flex: 1 1 50%;
    background-color: #fff;
    padding: 20px; }
  .section__logo {
    width: 190px;
    height: 100px;
    display: inline-block;
    position: relative; }
    .section__logo svg {
      position: absolute;
      width: 100%;
      height: 100%; }
      .section__logo svg:hover path {
        fill: #fdb460; }
      .section__logo svg path {
        fill: #fff;
        transition: 0.3s all; }
  .section__socials {
    display: flex;
    align-items: center;
    gap: 0 20px; }
  .section__header {
    display: flex;
    justify-content: space-between;
    padding: 0 20px; }
  .section__social-link {
    display: inline-block;
    position: relative;
    width: 30px;
    height: 30px; }
    .section__social-link svg {
      position: absolute;
      width: 100%;
      height: 100%; }
      .section__social-link svg:hover path {
        fill: #fdb460; }
      .section__social-link svg path {
        fill: #fff;
        transition: 0.3s all; }

.section__right {
  display: flex;
  align-items: center;
  justify-content: center; }

.quiz {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px 0;
  max-width: 750px;
  width: 100%;
  border: 2px solid black; }

.steps {
  font-size: 20px; }

.progress {
  width: 100%;
  background-color: #1d1d32;
  height: 5px;
  position: relative; }
  .progress span {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: 25%;
    background-color: #82cd47;
    height: 5px;
    transition: 0.3s all; }

.ipt-simple {
  min-height: 50px;
  border: 1px solid black;
  padding: 15px; }

.box {
  display: none;
  flex-direction: column;
  gap: 20px 0;
  opacity: 0;
  /* Set opacity to 0 for inactive tabs */
  transition: opacity 0.5s ease; }
  .box.active {
    display: flex;
    opacity: 1; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.question {
  font-size: 32px; }

.answers {
  display: flex;
  flex-direction: column;
  gap: 10px 0; }

.answer-wrapper {
  transition: 0.3s all;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  color: black;
  border: 1px solid black;
  padding-left: 60px;
  min-height: 60px;
  position: relative;
  cursor: pointer; }
  .answer-wrapper:hover {
    background-color: #d2f8b5c0;
    border: 1px solid #d2f8b5c0; }
    .answer-wrapper:hover .vector {
      border: 1px solid transparent; }
  .answer-wrapper.active {
    color: #fff;
    border: 1px solid #82cd47;
    background-color: #82cd47; }
    .answer-wrapper.active .vector {
      border: 1px solid transparent; }
      .answer-wrapper.active .vector svg {
        opacity: 1;
        fill: #82cd47; }
  .answer-wrapper .vector {
    border: 1px solid black;
    width: 30px;
    height: 30px;
    position: absolute;
    left: 15px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 20px;
    transition: 0.3s all; }
    .answer-wrapper .vector svg {
      position: absolute;
      width: 100%;
      height: 100%;
      transition: 0.3s all;
      opacity: 0; }
  .answer-wrapper input {
    display: none; }

.buttons {
  display: flex;
  justify-content: space-between; }

.button {
  padding: 15px;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  border-radius: 22px;
  max-width: 200px;
  width: 100%;
  transition: 0.3s all; }
  .button.out {
    display: none; }
  .button.prev {
    background-color: #5b3758; }
    .button.prev:hover {
      background-color: #a15269; }
  .button.next {
    margin-left: auto;
    background-color: #ff9f32; }
    .button.next:hover {
      background-color: #f88705; }
  .button.result, .button.restart {
    max-width: 300px;
    background-color: #ff9f32; }
    .button.result:hover, .button.restart:hover {
      background-color: #f88705; }

.result {
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.wrapper-img {
  max-width: 500px;
  width: 100%;
  height: 500px;
  position: relative;
  display: inline-block;
  margin-bottom: 20px; }
  .wrapper-img img {
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0; }

.title {
  font-size: 38px;
  margin-bottom: 30px;
  text-align: center; }

.descr {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px; }

@media screen and (max-width: 768px) {
  .buttons {
    flex-direction: column;
    gap: 20px 0; }
  .button {
    max-width: 100% !important; }
  .section {
    flex-direction: column; }
    .section__left, .section__right {
      height: 100vh;
      max-height: 100svh; }
  .question {
    font-size: 24px; } }

@media screen and (max-width: 500px) {
  .section__logo {
    width: 140px;
    height: 100px;
    display: inline-block;
    position: relative; }
    .section__logo svg {
      position: absolute;
      width: 100%;
      height: 100%; }
      .section__logo svg:hover path {
        fill: #fdb460; }
      .section__logo svg path {
        fill: #fff;
        transition: 0.3s all; } }
