@import "vars";
@import "global";
@import "fonts";

.section__left {
    position: relative;
    width: 100%;
    height: 100vh;
    background: #1d1d32;
    overflow: hidden;
}
section .air {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: url(https://1.bp.blogspot.com/-xQUc-TovqDk/XdxogmMqIRI/AAAAAAAACvI/AizpnE509UMGBcTiLJ58BC6iViPYGYQfQCLcBGAsYHQ/s1600/wave.png);
    background-size: 1000px 100px;
}
section .air.air1 {
    animation: wave 30s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
}
section .air.air2 {
    animation: wave2 15s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: -5s;
    bottom: 10px;
}
section .air.air3 {
    animation: wave 30s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: 15px;
}
section .air.air4 {
    animation: wave2 5s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -5s;
    bottom: 20px;
}
@keyframes wave {
    0% {
        background-position-x: 0px;
    }
    100% {
        background-position-x: 1000px;
    }
}
@keyframes wave2 {
    0% {
        background-position-x: 0px;
    }
    100% {
        background-position-x: -1000px;
    }
}
// #1d1d32; main violet
// #c99444; orange
// #FDB460 orange 2
// yellow #F9F871
//light violet #5B3758
//medium violet-orange #A15269
.section {
    display: flex;
    justify-content: space-between;
    &__left {
        flex: 1 1 50%;
    }

    &__right {
        flex: 1 1 50%;
        background-color: $white;
        padding: 20px;
    }
    &__logo {
        width: 190px;
        height: 100px;
        display: inline-block;
        position: relative;
        svg {
            position: absolute;
            width: 100%;
            height: 100%;
            &:hover {
                path {
                    fill: #fdb460;
                }
            }
            path {
                fill: $white;
                transition: 0.3s all;
            }
        }
    }

    &__socials {
        display: flex;
        align-items: center;
        gap: 0 20px;
    }
    &__header {
        display: flex;
        justify-content: space-between;
        padding: 0 20px;
    }
    &__social-link {
        display: inline-block;
        position: relative;
        width: 30px;
        height: 30px;
        svg {
            position: absolute;
            width: 100%;
            height: 100%;
            &:hover {
                path {
                    fill: #fdb460;
                }
            }
            path {
                fill: $white;
                transition: 0.3s all;
            }
        }
    }
}
.section {
    &__right {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
.quiz {
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px 0;
    max-width: 750px;
    width: 100%;
    border: 2px solid black;
}
.steps {
    font-size: 20px;
}
.progress {
    width: 100%;
    background-color: #1d1d32;
    height: 5px;
    position: relative;
    span {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 2;
        width: 25%;
        background-color: #82cd47;
        height: 5px;
        transition: 0.3s all;
    }
}
.ipt-simple {
    min-height: 50px;
    border: 1px solid black;
    padding: 15px;
}

.box {
    display: none;
    flex-direction: column;
    gap: 20px 0;

    opacity: 0; /* Set opacity to 0 for inactive tabs */
    transition: opacity 0.5s ease;
    &.active {
        display: flex;
        opacity: 1;
    }
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.question {
    font-size: 32px;
}
.answers {
    display: flex;
    flex-direction: column;
    gap: 10px 0;
}
.answer-wrapper {
    transition: 0.3s all;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    color: black;
    border: 1px solid black;
    padding-left: 60px;
    min-height: 60px;
    position: relative;
    cursor: pointer;
    &:hover {
        background-color: #d2f8b5c0;
        border: 1px solid #d2f8b5c0;
        .vector {
            border: 1px solid transparent;
        }
    }
    &.active {
        color: $white;
        border: 1px solid #82cd47;
        background-color: #82cd47;
        .vector {
            border: 1px solid transparent;
            svg {
                opacity: 1;
                fill: #82cd47;
            }
        }
    }
    .vector {
        border: 1px solid black;
        width: 30px;
        height: 30px;
        position: absolute;
        left: 15px;
        border-radius: 50%;
        background-color: $white;
        margin-right: 20px;
        transition: 0.3s all;

        svg {
            position: absolute;
            width: 100%;
            height: 100%;
            transition: 0.3s all;
            opacity: 0;
        }
    }
    input {
        display: none;
    }
}
.buttons {
    display: flex;
    justify-content: space-between;
}
.button {
    padding: 15px;
    &.out {
        display: none;
    }
    color: $white;
    font-size: 16px;
    font-weight: 700;
    // text-transform: uppercase;
    border-radius: 22px;
    max-width: 200px;
    width: 100%;
    transition: 0.3s all;
    &.prev {
        background-color: #5b3758;
        &:hover {
            background-color: #a15269;
        }
    }
    &.next {
        margin-left: auto;
        background-color: #ff9f32;
        &:hover {
            background-color: #f88705;
        }
    }
    &.result,
    &.restart {
        max-width: 300px;
        // border: 1px solid #82cd47;
        background-color: #ff9f32;
        &:hover {
            background-color: #f88705;
        }
    }
}
.result {
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.wrapper-img {
    max-width: 500px;
    width: 100%;
    height: 500px;
    position: relative;
    display: inline-block;
    margin-bottom: 20px;
    img {
        width: 100%;
        object-fit: cover;
        object-position: center;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}
.title {
    font-size: 38px;
    margin-bottom: 30px;
    text-align: center;
}
.descr {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
}
.image {
}

@media screen and (max-width: 768px) {
    .buttons {
        flex-direction: column;
        gap: 20px 0;
    }
    .button {
        max-width: 100% !important;
    }
    .section {
        &__left,
        &__right {
            height: 100vh;
            max-height: 100svh;
            //qweqweqweqwe
        }
        flex-direction: column;
    }
    .question {
        font-size: 24px;
    }
}
@media screen and (max-width: 500px) {
    .section {
        &__logo {
            width: 140px;
            height: 100px;
            display: inline-block;
            position: relative;
            svg {
                position: absolute;
                width: 100%;
                height: 100%;
                &:hover {
                    path {
                        fill: #fdb460;
                    }
                }
                path {
                    fill: $white;
                    transition: 0.3s all;
                }
            }
        }
    }
}
